<template>
  <label
    class="has-checkbox page__body__header__label text--capital mb-3 text--black"
    v-if="label"
  >
    {{ label }}
    <slot name="check">
      <input
        type="checkbox"
        :value="value"
        @change="$emit('input', $event.target.checked)"
      />
    </slot>
    <span class="checkmark"></span>
  </label>
</template>

<script>
export default {
  name: "BaseCheckbox",
  inheritAttrs: false,
  props: {
    label: {
      type: String,
      description: "Input label"
    },
    value: {
      type: [String, Number, Array, Boolean],
      description: "Input value"
    }
  },

  model: {
    prop: "value",
    event: "input"
  },

  data() {
    return {
      focused: false
    };
  },

  computed: {
    listeners() {
      return {
        ...this.$listeners,
        input: this.onInput,
        blur: this.onBlur,
        focus: this.onFocus
      };
    }
  },

  methods: {
    onInput(evt) {
      this.$emit("input", evt.target.value);
    },
    onFocus() {
      this.focused = true;
    },
    onBlur() {
      this.focused = false;
    }
  }
}
</script>
